// pages / dashboard

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../style/dashboard.module.css';
import '../../style/theme.css';

// Action
// import {setUser, logOutUser} from '../../action/UserAction';

// Components
import ChartPayments from '../../components/dashboard/charts/ChartPayments';


class DashboardHome extends React.Component {

    toPayment() {
        navigateTo('/dashboard/payment');
    }

    render() {
        if (this.props.user.loggedIn) {
            return (
                <div className="wrapper wrapper-content">

                  {/* Start of Helmet */}
                  <Helmet title="Dashboard - overview status" meta={[
                      {
                          name: 'description',
                          content: 'Dashboard'
                      }, {
                          name: 'keywords',
                          content: 'Dashboard'
                      }
                  ]}/>
                  {/* End of Helmet */}


                  {/* Start of Content */}
                    <div className="row">
                        <div className="col-md-12">
                            <div className="ibox float-e-margins">
                                <div className="ibox-content">
                                    <div className="row">
                                        <div className="col-md-3">
                                            <div>
                                                <h2 className="m-b-xs">
                                                  Welcome, {this.props.user.first_name + ' ' + this.props.user.last_name}!
                                                </h2>

                                                <div className="space-30"></div>

                                                <h4>Next Payment Due</h4>
                                                <div className="hr-line-dashed"></div>
                                                <table className="table table-hover no-margins">
                                                    <thead>
                                                        <tr>
                                                            <th>Bill Type</th>
                                                            <th>Due Date</th>
                                                            <th>Total Amount</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr>
                                                            <td><small>Electric Bill</small></td>
                                                            <td>
                                                              <small>
                                                                <i className="fa fa-clock-o"></i> September, 3
                                                              </small>
                                                            </td>
                                                            <td className="text-navy"> RM 58.65 </td>
                                                        </tr>
                                                        <tr>
                                                            <td><small>Water Bill</small></td>
                                                            <td>
                                                              <small>
                                                                <i className="fa fa-clock-o"></i> August, 31
                                                              </small>
                                                            </td>
                                                            <td className="text-navy"> RM 12.80 </td>
                                                        </tr>
                                                        <tr>
                                                            <td>
                                                                <small>Maintenance Bill</small>
                                                            </td>
                                                            <td>
                                                              <small>
                                                                <i className="fa fa-clock-o"></i> September, 15
                                                              </small>
                                                            </td>
                                                            <td className="text-navy"> RM 25.75 </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                                <div className="space-25"></div>
                                                <div className="text-right">
                                                    <button id="print-disable" className="btn btn-primary" onClick={this.toPayment.bind(this)}>
                                                        <i className="fa fa-list-alt"></i> View Payment
                                                    </button>
                                                </div>

                                            </div>
                                        </div>
                                        <div className="col-md-9">
                                            <div>
                                                <span className="pull-right text-right">
                                                <small>Average value of payment in the past month in: <strong>July</strong></small>
                                                    <br/>
                                                    Approximate: RM 60.00
                                                </span>

                                                <div className="space-30"></div>
                                                <h3 className="font-bold no-margins">
                                                    Payment History Overview
                                                </h3>
                                                <small>
                                                    Chart of payments for 7-months
                                                </small>
                                            </div>

                                            <ChartPayments/>

                                            <div className="m-t-md">
                                                <small className="pull-right">
                                                    <i className="fa fa-clock-o"></i> Update on 16.07.2017
                                                </small>
                                                {/*
                                                <small>
                                                   <strong>Analysis of payment:</strong> The value has been changed over time, and last month reached a level over $50,000.
                                                </small>
                                                */}
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>



                        <div className="col-md-4">
                            <div className="ibox float-e-margins">
                                <div className="ibox-title">
                                    <h5>Announcements</h5>
                                    <div className="ibox-tools">
                                        <a className="collapse-link">
                                            <i className="fa fa-chevron-up"></i>
                                        </a>
                                        <a className="close-link">
                                            <i className="fa fa-times"></i>
                                        </a>
                                    </div>
                                </div>
                                <Link className="ibox-content ibox-heading feed-element-column" to="/dashboard/announcement">
                                    <h3><i className="fa fa-bullhorn"></i> Powerhouse Maintenance</h3>
                                    <small>
                                        There will be power shortage on 3pm today. Repair duration is estimated around 30 minutes.
                                    </small>
                                </Link>
                                <div className="ibox-content">
                                    <div className="feed-activity-list">

                                        <div className="feed-element">
                                            <Link to="/dashboard/announcement" className="feed-element-column">
                                                <small className="pull-right text-navy">4 days ago</small>
                                                <strong>Lift 1 Reparing</strong>
                                                <div>There will be a repair on lift 1 today. Repair duration is estimated around 45 minutes</div>
                                                <small className="text-muted">13.07.2017 - 5:60 pm</small>
                                            </Link>
                                        </div>

                                        <div className="feed-element">
                                            <Link to="/dashboard/announcement" className="feed-element-column">
                                                <small className="pull-right">6 days ago</small>
                                                <strong>Resignation of security: Johanas Russel</strong>
                                                <div>Johanas Russel will resign as security on 15 June 2017.</div>
                                                <small className="text-muted">11.06.2017 - 2:23 pm</small>
                                            </Link>
                                        </div>

                                        <div className="feed-element">
                                            <Link to="/dashboard/announcement" className="feed-element-column">
                                                <small className="pull-right">1 month ago</small>
                                                <strong>Powerhouse Maintenance</strong>
                                                <div>There will be power shortage on 3pm today. Repair duration is estimated around 30 minutes.</div>
                                                <small className="text-muted">08.06.2017 - 1:00 pm</small>
                                            </Link>
                                        </div>

                                        <div className="feed-element">
                                            <Link to="/dashboard/announcement" className="feed-element-column">
                                                <small className="pull-right">1 month ago</small>
                                                <strong>Water Tank Maintenance</strong>
                                                <div>There will be no water usage on 8.30pm today. Repair duration is estimated around 30 minutes.</div>
                                                <small className="text-muted">08.06.2017 - 8:48 pm</small>
                                            </Link>
                                        </div>

                                        <div className="feed-element">
                                            <Link to="/dashboard/announcement" className="feed-element-column">
                                                <small className="pull-right">5m ago</small>
                                                <strong>New Admin on Board</strong>
                                                <div>Jane Siddleback will join the management team on 12 March 2017.</div>
                                                <small className="text-muted">10.02.2017 - 8:48 pm</small>
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div className="col-md-4">
                            <div className="ibox float-e-margins">
                                <div className="ibox-title">
                                    <h5>Messages</h5>
                                    <div className="ibox-tools">
                                        <a className="collapse-link">
                                            <i className="fa fa-chevron-up"></i>
                                        </a>
                                        <a className="close-link">
                                            <i className="fa fa-times"></i>
                                        </a>
                                    </div>
                                </div>
                                <Link className="ibox-content ibox-heading feed-element-column" to="/dashboard/conversation">
                                    <h3><i className="fa fa-envelope-o"></i> New messages</h3>
                                    <small><i className="fa fa-tim"></i> You have 22 new messages.</small>
                                </Link>
                                <div className="ibox-content">
                                    <div className="feed-activity-list">

                                        <div className="feed-element">
                                            <Link to="/dashboard/conversation" className="feed-element-column">
                                                <small className="pull-right text-navy">1m ago</small>
                                                <strong>Monica Smith</strong>
                                                <div>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum</div>
                                                <small className="text-muted">12.06.2017 - 5:60 pm</small>
                                            </Link>
                                        </div>

                                        <div className="feed-element">
                                            <Link to="/dashboard/conversation" className="feed-element-column">
                                                <small className="pull-right">2m ago</small>
                                                <strong>Jogn Angel</strong>
                                                <div>There are many variations of passages of Lorem Ipsum available</div>
                                                <small className="text-muted">11.06.2017 - 2:23 pm</small>
                                            </Link>
                                        </div>

                                        <div className="feed-element">
                                            <Link to="/dashboard/conversation" className="feed-element-column">
                                                <small className="pull-right">5m ago</small>
                                                <strong>Jesica Ocean</strong>
                                                <div>Contrary to popular belief, Lorem Ipsum</div>
                                                <small className="text-muted">08.06.2017 - 1:00 pm</small>
                                            </Link>
                                        </div>

                                        <div className="feed-element">
                                            <Link to="/dashboard/conversation" className="feed-element-column">
                                                <small className="pull-right">5m ago</small>
                                                <strong>Monica Jackson</strong>
                                                <div>The generated Lorem Ipsum is therefore </div>
                                                <small className="text-muted">10.06.2017 - 8:48 pm</small>
                                            </Link>
                                        </div>


                                        <div className="feed-element">
                                            <Link to="/dashboard/conversation" className="feed-element-column">
                                                <small className="pull-right">5m ago</small>
                                                <strong>Anna Legend</strong>
                                                <div>All the Lorem Ipsum generators on the Internet tend to repeat </div>
                                                <small className="text-muted">10.06.2017 - 8:48 pm</small>
                                            </Link>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>




                        <div className="col-md-4">
                            <div className="ibox float-e-margins">
                                <div className="ibox-title">
                                    <h5>Residency.my News</h5>
                                    <div className="ibox-tools">
                                        <a className="collapse-link">
                                            <i className="fa fa-chevron-up"></i>
                                        </a>
                                        <ul className="dropdown-menu dropdown-user">
                                            <li><a href="#">Config option 1</a>
                                            </li>
                                            <li><a href="#">Config option 2</a>
                                            </li>
                                        </ul>
                                        <a className="close-link">
                                            <i className="fa fa-times"></i>
                                        </a>
                                    </div>
                                </div>

                                <div className="ibox-content inspinia-timeline">

                                    <div className="timeline-item">
                                        <div className="row">
                                            <div className="col-xs-3 date">
                                                <i className="fa fa-file-text"></i> 13.07.2017
                                                <br/>
                                                <small className="text-navy">2 days ago</small>
                                            </div>
                                            <Link className="col-xs-7 content no-top-border feed-element-column" to="/dashboard/news">
                                                <p className="m-b-xs"><strong>New Added Features in v2.1!</strong></p>
                                                <p>
                                                    There will be new features added in Residency.my version 2.1, which will be release on 21st September 2017.
                                                </p>
                                            </Link>
                                        </div>
                                    </div>
                                    <div className="timeline-item">
                                        <div className="row">
                                            <div className="col-xs-3 date">
                                                <i className="fa fa-wrench"></i> 12.07.2017
                                                <br/>
                                                <small className="text-navy">3 days ago</small>
                                            </div>
                                            <Link className="col-xs-7 content no-top-border feed-element-column" to="/dashboard/news">
                                                <p className="m-b-xs"><strong>Server Maintenance</strong></p>

                                                <p>
                                                    There will be a server maintenace at 3pm on 31 August 2017. Reason is to resolve the bug issues.
                                                    Sorry for the inconvenience caused.
                                                </p>
                                            </Link>
                                        </div>
                                    </div>
                                    <div className="timeline-item">
                                        <div className="row">
                                            <div className="col-xs-3 date">
                                                <i className="fa fa-coffee"></i> 25.06.2017
                                                <br/>
                                                  <small className="text-navy">1 month ago</small>
                                            </div>
                                            <Link className="col-xs-7 content no-top-border feed-element-column" to="/dashboard/news">
                                                <p className="m-b-xs"><strong>Improvement on Servers</strong></p>
                                                <p>
                                                    Residency.my have acquired servers Acquila in Subang Jaya for high performance internet service.
                                                </p>
                                            </Link>
                                        </div>
                                    </div>
                                    <div className="timeline-item">
                                        <div className="row">
                                            <div className="col-xs-3 date">
                                                <i className="fa fa-phone"></i> 05.06.2017
                                                <br/>
                                                <small className="text-navy">1 month ago</small>
                                            </div>
                                            <Link className="col-xs-7 content no-top-border feed-element-column" to="/dashboard/news">
                                                <p className="m-b-xs"><strong>Our New Office!</strong></p>
                                                <p>
                                                    Our new office is now located in MSC area Bangsar South in KL!
                                                </p>
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                    </div>
                </div>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(DashboardHome);



