// pages / dashboard / charts / ChartPayments

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class ChartPayments extends React.Component {
    componentDidMount() {
        let lineData = {
            labels: ["January", "February", "March", "April", "May", "June", "July"],
            datasets: [
                {
                    label: "Electric Bill",
                    backgroundColor: "rgba(26,179,148,0.5)",
                    borderColor: "rgba(26,179,148,0.7)",
                    pointBackgroundColor: "rgba(26,179,148,1)",
                    pointBorderColor: "#fff",
                    data: [28, 48, 40, 19, 86, 27, 90]
                },
                {
                    label: "Water Bill",
                    backgroundColor: "rgba(102, 204, 255,0.5)",
                    borderColor: "rgba(102, 204, 255,0.7)",
                    pointBackgroundColor: "rgba(102, 204, 255,1)",
                    pointBorderColor: "#fff",
                    data: [75, 20, 85, 30, 21, 80, 50]
                },
                {
                    label: "Maintenance Bill",
                    backgroundColor: "rgba(220,220,220,0.5)",
                    borderColor: "rgba(220,220,220,1)",
                    pointBackgroundColor: "rgba(220,220,220,1)",
                    pointBorderColor: "#fff",
                    data: [65, 59, 80, 81, 56, 55, 40]
                }
            ]
        };

        let lineOptions = {
            responsive: true
        };

        let ctx = document.getElementById("lineChart").getContext("2d");
        new Chart(ctx, {type: 'line', data: lineData, options:lineOptions});
    }

    render() {
        return (
            <div>
                <canvas id="lineChart" height="70"></canvas>
            </div>
        );
    }
};

const mapStateToProps = (state) => {
    return {};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(ChartPayments);



